@import url('https://fonts.googleapis.com/css2?family=Commissioner:wght@400;500;700;800&display=swap');


$ModerateCyan: hsl(176, 50%, 47%);
$DarkCyan: hsl(176, 72%, 28%);
$Black: hsl(0, 0%, 0%);
$LightGray: hsl(195, 17%, 91%);
$DarkGray: hsl(0, 0%, 48%);
$Headerweight: 800;
$Buttonweight: 700;


body {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Commissioner', sans-serif;
    font-size: 16px;
}

.pad {
    padding: 0 30px;

    @media (min-width:800px) {
        padding: 0 30px;
    }
}

.pledges-wrapper {
    display: flex;
    flex-direction: column;
    align-items: left;
    justify-content: center;
    text-align: left;
    background-color: white;
    border: 1px solid $LightGray;
    width: 70vw;
    margin: 0 auto;
    border-radius: 12px;
    padding: 20px 40px;
    position: relative;
    margin-top: 40%;
    position: absolute;
    z-index: 800;
    left: 0;
    right: 0;
    // .openPledge{
    //     max-height: 300px;
    //     .slots{
    //         margin-bottom: -90px;
    //     }
    //   }


    @media (min-width:800px) {
        width: 55vw;
        padding: 20px 50px;
        align-items: flex-start;
        margin-top: 50px;
    }

    label .default {
        font-size: 1.2rem;
        font-weight: $Buttonweight;
        position: relative;



        &:hover {
            color: $ModerateCyan;
        }

        // .rad{
        //     position: absolute;
        //     width: 1rem;
        //     height: 1rem;
        //     left: 1px;
        //     top: 2px;
        //     background-color: transparent;
        //     border: 1px solid $ModerateCyan;
        //     border-radius: 50px;
        //     display: inline-flex;
        //     padding: 3px;
        //   transform: scale(0);

        //     &::after{
        //         content: '';
        //         display: block;
        //         width: 100%;
        //         height: 100%;
        //         background-color:$ModerateCyan;
        //         border-radius: 50px;
        //     }
        // }
    }

    input[type='radio'] {
        transform: scale(2);
        margin-right: 1.2rem;
        font-size: 1.5rem;

        // &:checked + .pad::after{
        //     transform: scale(1);
        // }

    }



    input[type='number'] {
        font-size: 1.3rem;
        padding-left: 35px;
        color: black;
        margin-bottom: 12px;

        @media (min-width:900px) {
            margin-bottom: 0;
        }
    }

    .Modalhead {
        display: flex;
        justify-content: space-between;
        align-items: center;
        cursor: pointer;
        width: 100%;


    }

    .pledge-card {
        display: flex;
        flex-direction: column;
        align-items: left;
        text-align: left;
        background-color: white;
        border: 2px solid $LightGray;
        margin: 0 auto;
        border-radius: 12px;
        padding: 30px 10px 50px 10px;
        position: relative;
        margin-bottom: 20px;
        overflow-x: hidden;

        // &:hover {
        //     padding: 30px 10px 35px 10px;
        // }

        &:hover .bottom {
            display: flex;
            margin-top: 70px;
        }

        &:hover .slots {
            margin-bottom: 220px;

            @media (min-width:900px) {
                margin-bottom: 120px;

            }

            &::after {
                content: '';
                position: absolute;
                bottom: 10px;
                height: 2px;
                background-color: $LightGray;
                bottom: 2%;
                left: -90px;
                right: 0;
                width: 1500px;
            }
        }

        @media (min-width:900px) {
            margin-bottom: 30px;
            padding: 30px 10px 70px 10px;

            .body {
                margin-bottom: 90px;
            }
        }

        .head,
        .bottom {

            .heading {
                margin: 0;

            }

            .pledge {
                color: $ModerateCyan;
                font-size: clamp(1rem, 2.5vw, 1.3rem);
                margin-left: 2.5rem;
            }

            @media (min-width:900px) {
                display: flex;
                justify-content: space-between;
                align-items: center;
            }
        }

        .body {
            font-size: 1rem;
            color: $DarkGray;
            line-height: 2rem;
            margin-bottom: 10px;

            @media (min-width:800px) {
                font-size: 1.5rem;
                line-height: 2.5rem;
                margin-bottom: 90px;
            }

        }


        .slots {

            display: flex;
            align-items: center;
            gap: 5px;
            font-size: 1rem;
            font-weight: $Buttonweight;
            position: absolute;
            bottom: 0%;

            @media (min-width:800px) {
                bottom: 4%;
            }

            // &::after {
            //     content: '';
            //     position: absolute;
            //     bottom: 10px;
            //     height: 2px;
            //     background-color: $LightGray;
            //     bottom: 2%;
            //     left: -90px;
            //     right: 0;
            //     width: 1500px;
            // }


            display: flex;
            align-items: center;
            gap: 5px;
            font-size: 1rem;
            font-weight: $Buttonweight;


            p {
                font-size: 1rem;
                color: $DarkGray;
            }
        }

        .hide {
            display: none;
        }

        @media (min-width:800px) {
            padding: 15px 30px;
        }

        &:hover {
            border: 2px solid $ModerateCyan;

        }

        .bottom {

            display: none;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            margin-top: 40px;
            width: 100%;

            @media (min-width:900px) {
                flex-direction: row;
                margin-top: 50px;

                .Enter {
                    width: 100%;
                    color: $DarkCyan;
                    font-size: 1.2rem;
                }
            }

            .pledge-container {
                display: flex;
                flex-direction: column;
                width: 100%;
                justify-content: center;
                align-items: center;

                // transform: scale(.9);

                @media (min-width:900px) {
                    flex-direction: row;
                    justify-content: flex-end;
                    align-items: center;
                }
            }
        }
    }

    .out-of-stock {
        opacity: .3;

        .reward {
            background-color: $DarkGray;
        }
    }



}